import React, { useEffect, useRef, useState } from "react";
import "./styles/Map.css";

function Map({ onLocationSelect, mode = "view", initialSpot = null }) {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const [selectedLocation, setSelectedLocation] = useState(null);
  const [userLocation, setUserLocation] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [parkingSpots, setParkingSpots] = useState([]);

  // 获取所有停车位信息
  useEffect(() => {
    const fetchParkingSpots = async () => {
      try {
        const response = await fetch('http://localhost:3000/parking-spots');
        const data = await response.json();
        setParkingSpots(data);
      } catch (error) {
        console.error('获取停车位失败:', error);
      }
    };

    if (mode !== 'detail') {
      fetchParkingSpots();
    }
  }, [mode]);

  // 获取用户位置
  useEffect(() => {
    if (mode === 'detail' && initialSpot) {
      const [lat, lng] = initialSpot.coordinates.split(',');
      setUserLocation({ lat: parseFloat(lat), lng: parseFloat(lng) });
      setLoading(false);
      return;
    }

    const getLocation = async () => {
      if ("geolocation" in navigator) {
        try {
          const position = await new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(resolve, reject, {
              enableHighAccuracy: true,
              timeout: 10000,
              maximumAge: 0
            });
          });
          
          setUserLocation({
            lat: position.coords.latitude,
            lng: position.coords.longitude
          });
        } catch (error) {
          setUserLocation({
            lat: 39.915,
            lng: 116.404
          });
        } finally {
          setLoading(false);
        }
      } else {
        setUserLocation({
          lat: 39.915,
          lng: 116.404
        });
        setLoading(false);
      }
    };

    getLocation();
  }, [mode, initialSpot]);

  // 初始化地图
  useEffect(() => {
    if (!userLocation || !mapRef.current || !window.BMapGL) return;

    try {
      const map = new window.BMapGL.Map(mapRef.current);
      mapInstanceRef.current = map;

      if (mode === "detail" && initialSpot) {
        const [lat, lng] = initialSpot.coordinates.split(',');
        const point = new window.BMapGL.Point(lng, lat);
        map.centerAndZoom(point, 16);
        
        const marker = new window.BMapGL.Marker(point);
        map.addOverlay(marker);

        const infoWindow = new window.BMapGL.InfoWindow(`
          <div style="padding: 8px;">
            <h4>${initialSpot.location}</h4>
            <p>价格: ¥${initialSpot.price}/小时</p>
            <p>联系方式: ${initialSpot.contact}</p>
          </div>
        `);

        marker.addEventListener('click', () => {
          map.openInfoWindow(infoWindow, point);
        });
        
        map.openInfoWindow(infoWindow, point);
      } else {
        const point = new window.BMapGL.Point(userLocation.lng, userLocation.lat);
        map.centerAndZoom(point, 15);
        map.enableScrollWheelZoom(true);
        
        map.addControl(new window.BMapGL.NavigationControl());
        map.addControl(new window.BMapGL.ScaleControl());
        map.addControl(new window.BMapGL.ZoomControl());
        
        const userMarker = new window.BMapGL.Marker(point);
        map.addOverlay(userMarker);
        
        parkingSpots.forEach(spot => {
          if (!spot.coordinates) return;
          
          const [lat, lng] = spot.coordinates.split(',');
          const spotPoint = new window.BMapGL.Point(lng, lat);
          const marker = new window.BMapGL.Marker(spotPoint);
          map.addOverlay(marker);

          const infoWindow = new window.BMapGL.InfoWindow(`
            <div style="padding: 8px;">
              <h4>${spot.location}</h4>
              <p>价格: ¥${spot.price}/小时</p>
              <p>联系方式: ${spot.contact}</p>
            </div>
          `);

          marker.addEventListener('click', () => {
            map.openInfoWindow(infoWindow, spotPoint);
          });
        });
        
        if (mode === "select") {
          map.addEventListener("click", (e) => {
            const newLocation = {
              lat: e.latlng.lat,
              lng: e.latlng.lng,
            };
            setSelectedLocation(newLocation);
            if (onLocationSelect) {
              onLocationSelect(newLocation);
            }
            
            map.clearOverlays();
            map.addOverlay(userMarker);
            const newMarker = new window.BMapGL.Marker(e.latlng);
            map.addOverlay(newMarker);
          });
        }
      }
    } catch (err) {
      setError('地图加载失败，请刷新重试');
    }

    return () => {
      if (mapInstanceRef.current) {
        mapInstanceRef.current.destroy();
      }
    };
  }, [userLocation, parkingSpots, mode, onLocationSelect, initialSpot]);

  if (loading) {
    return <div className="map-loading">正在加载地图...</div>;
  }

  if (error) {
    return <div className="map-error">{error}</div>;
  }

  return (
    <div className="map-container">
      <div className="map-wrapper">
        <div ref={mapRef} className="map" />
        {mode === "select" && selectedLocation && (
          <div className="location-info">
            <h3>选中的位置</h3>
            <p>纬度: {selectedLocation.lat}</p>
            <p>经度: {selectedLocation.lng}</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default Map;
