import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from 'jwt-decode';
import './styles/Auth.css';

function Auth() {
  const [isLogin, setIsLogin] = useState(true);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [fullName, setFullName] = useState('');
  const [phone, setPhone] = useState('');
  const [avatar, setAvatar] = useState('');
  const [bio, setBio] = useState('');
  const [address, setAddress] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    const endpoint = isLogin ? 'login' : 'register';
    const body = isLogin 
      ? { username, password }
      : {
          username,
          password,
          full_name: fullName,
          phone,
          avatar,
          bio,
          address
        };
    
    try {
      const response = await fetch(`http://localhost:3000/${endpoint}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(body),
      });

      const contentType = response.headers.get("content-type");
      if (!contentType || !contentType.includes("application/json")) {
        throw new Error("服务器返回格式错误");
      }

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || `${isLogin ? '登录' : '注册'}失败`);
      }

      if (isLogin) {
        localStorage.setItem('username', username);
        alert('登录成功！');
        navigate('/messages');
      } else {
        alert('注册成功！请登录。');
        setIsLogin(true);
      }
      
      setUsername('');
      setPassword('');
      
    } catch (err) {
      console.error('Error:', err);
      setError(err.message || '发生错误，请重试。');
    } finally {
      setIsLoading(false);
    }
  };

  const handleGoogleSuccess = async (credentialResponse) => {
    try {
      const decoded = jwtDecode(credentialResponse.credential);
      const googleUsername = decoded.email.split('@')[0]; // 使用邮箱前缀作为用户名
      
      // 发送 Google 登录信息到服务器
      const response = await fetch('http://localhost:3000/google-login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          googleId: decoded.sub,
          email: decoded.email,
          username: googleUsername,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || 'Google 登录失败');
      }

      localStorage.setItem('username', googleUsername);
      alert('Google 登录成功！');
      navigate('/messages');
    } catch (err) {
      console.error('Google login error:', err);
      setError(err.message || 'Google 登录失败');
    }
  };

  const handleGoogleError = () => {
    setError('Google 登录失败，请重试');
  };

  return (
    <div className="auth-container">
      <form onSubmit={handleSubmit} className="auth-form">
        <h2>{isLogin ? '登录' : '注册'}</h2>
        
        {error && <div className="error-message">{error}</div>}
        
        <input
          type="text"
          placeholder="用户名"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          required
          disabled={isLoading}
        />
        
        <input
          type="password"
          placeholder="密码"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          disabled={isLoading}
        />

        {!isLogin && (
          <>
            <input
              type="text"
              placeholder="姓名"
              value={fullName}
              onChange={(e) => setFullName(e.target.value)}
              required
              disabled={isLoading}
            />
            
            <input
              type="tel"
              placeholder="手机号"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              required
              disabled={isLoading}
            />
            
            <input
              type="file"
              accept="image/*"
              onChange={(e) => {
                const file = e.target.files[0];
                const reader = new FileReader();
                reader.onloadend = () => {
                  setAvatar(reader.result);
                };
                if (file) {
                  reader.readAsDataURL(file);
                }
              }}
            />
            
            <textarea
              placeholder="个人简介（选填）"
              value={bio}
              onChange={(e) => setBio(e.target.value)}
              disabled={isLoading}
            />
            
            <input
              type="text"
              placeholder="家庭住址（选填）"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
              disabled={isLoading}
            />
          </>
        )}
        
        <button type="submit" disabled={isLoading || !username || !password}>
          {isLoading ? '处理中...' : (isLogin ? '登录' : '注册')}
        </button>

        <div className="divider">
          <span>或</span>
        </div>

        <div className="google-login-container">
          <GoogleLogin
            onSuccess={handleGoogleSuccess}
            onError={handleGoogleError}
            text="继续使用 Google"
            shape="rectangular"
            locale="zh_CN"
          />
        </div>

        <p className="switch-mode">
          {isLogin ? "还没有账号？ " : "已有账号？ "}
          <button 
            type="button" 
            className="switch-button"
            onClick={() => setIsLogin(!isLogin)}
          >
            {isLogin ? '注册' : '登录'}
          </button>
        </p>
      </form>
    </div>
  );
}

export default Auth;
